<template>
  <div class="screen-container">
    <div class="screen-btn-group">
      <button
        class="screen-btn" @click="handleClassify('picture')"
        :class="{'screen-primary':content_type===0}">
        图片
      </button>
      <button
        v-if="$route.params.type!=='bottom'"
        class="screen-btn" @click="handleClassify('video')"
        :class="{'screen-primary':content_type===1}">
        视频
      </button>
      <button class="add-btn" @click="handleAdd">创建文件夹</button>
    </div>
    <div class="screen-body" v-loading="isLoading">
      <div class="screen-empty" v-if="isEmpty">
        <span>暂无数据</span>
      </div>
      <div class="screen-empty" v-else-if="isNoPower">
        <img src="~@/assets/empty_logo.gif" alt="empty_logo">
        <p class="empty-title">没有模块权限，请与管理员确认</p>
      </div>
      <template v-else>
        <div class="screen-body-container">
          <ul class="tab-list">
            <li v-for="(folder,i) in listData.data" :key="i" ref="folder" tabindex="0" @blur="showOption=false">
              <i v-if="Number(folder.status)===1" class="folder-status">播</i>
              <button class="folder-more" @click="handleMore(folder.id,i)">
                <i class="iconfont gym-icon-more"></i>
              </button>
              <transition name="el-zoom-in-top">
                <ul class="folder-option" v-show="showOption===folder.id">
                  <template v-if="folder.empty">
                    <li @click="handleDelete(folder)">删除</li>
                  </template>
                  <template v-else>
                    <template v-if="Number(folder.status)===1">
                      <li @click.once="changeStatus(folder)">停播</li>
                      <li>
                        <Poptip class="tip-box" trigger="hover" width="240" :transfer="false" placement="bottom-end">
                          <span class="operate-btn disable-del">编辑</span>
                          <div slot="content">
                            <ul class="tips-container">
                              <li>在播文件夹信息不能编辑与删除</li>
                              <li>若需要编辑或删除，请先停播文件夹</li>
                            </ul>
                          </div>
                        </Poptip>
                      </li>
                      <li>
                        <Poptip class="tip-box" trigger="hover" width="240" :transfer="false" placement="bottom-end">
                          <span class="operate-btn disable-del">删除</span>
                          <div slot="content">
                            <ul class="tips-container">
                              <li>在播文件夹信息不能编辑与删除</li>
                              <li>若需要编辑或删除，请先停播文件夹</li>
                            </ul>
                          </div>
                        </Poptip>
                      </li>
                    </template>
                    <template v-else>
                      <li @click.one="changeStatus(folder)">复播</li>
                      <li @click="handleEdit(folder)">编辑</li>
                      <li @click="handleDelete(folder)">删除</li>
                    </template>
                  </template>
                </ul>
              </transition>
              <p class="folder-title text-ellipsis" :title="folder.folderName">{{folder.folderName}}</p>
              <router-link
                tag="div"
                :title="folder.folderName"
                class="folder-icon"
                :to="$route.params.type+'/folder/'+folder.id">
                <i></i>
              </router-link>
              <div class="folder-time">
                <template v-if="Number(folder.unlimitedTime)===0">
                  不限时间
                </template>
                <template v-else-if="Number(folder.unlimitedTime)===1">
                  <span>{{folder.startTime}}</span>
                  <span style="margin: 0 14px;">至</span>
                  <span>{{folder.endTime}}</span>
                </template>
                <template v-else-if="Number(folder.unlimitedTime)===2">
                  <div>
                    <p class="folder-time-range">
                      {{folder.rePlayTime.split('-').map(v => v.slice(0, -3)).join(' - ')}}
                    </p>
                    <div class="folder-check-time">
                      <p>已选日期</p>
                      <DatePicker
                        type="date"
                        class="selected-time-picker"
                        size="default"
                        :editable="false"
                        :transfer="false"
                        :clearable="false"
                        multiple
                        :confirm="false"
                        :value="folder.rePlayDate"
                      />
                    </div>
                  </div>
                </template>
              </div>
            </li>
          </ul>
        </div>
        <div class="screen-body-footer">
          <Page
            :total="Number(listData.total)"
            :current="Number(listData.pageNum)"
            :page-size="Number(listData.pageSize)"
            show-sizer show-elevator show-total :transfer="false"
            @on-change="pageNumChange"
            @on-page-size-change="pageSizeChange"
          />
        </div>
      </template>
    </div>
    <ModalBody :visible="showDetail" :title="detailFolder?'编辑文件夹':'创建文件夹'" width="380">
      <template slot="icon">
        <i class="modal-class-icon"></i>
      </template>
      <DetailForm v-if="showDetail" :id="detailFolder" :isReplay="isReplay"
                  @confirm="handleConfirm(true)" @cancel="handleConfirm(false)"/>
    </ModalBody>
  </div>
</template>
<script>
import { getFolderList, stopPlayFolder, deleteFolder, cancelStatus } from 'api/screen'
import throttle from 'lodash/throttle'
import ModalBody from '../components/ModalBody'
import DetailForm from './components/DetailForm'

export default {
  components: {ModalBody, DetailForm},
  data () {
    return {
      isLoading: false,
      isEmpty: false,
      isNoPower: false,
      canSpread: false,
      isSpread: false,
      idNeedSpread: [],
      listData: {},
      spreadFolder: [],
      showOption: false,
      showDetail: false,
      detailFolder: false,
      isReplay: false
    }
  },
  computed: {
    tableData: function () {
      if (!Array.isArray(this.listData.data)) {
        return
      }
      let deletedMachines = {}
      if (Array.isArray(this.listData.machineCount)) {
        this.listData.machineCount.forEach((v) => {
          if (!deletedMachines[v.folder_id]) {
            deletedMachines[v.folder_id] = []
          }
          deletedMachines[v.folder_id].push(v)
        })
      }
      let storesArray = []
      this.listData.data.forEach((value) => {
        let storesObject = {}
        let storesName = value.clubName.split(',')
        let storesIds = 'fake_store_id'.split(',')
        let machinesName = (value.clubName + '-1').split(',')
        let machineIds = 'fake_machine_id'.split(',')
        // 剔除有问题的终端机
        let checkedMachineIds = 'fake_machine_id'.split(',')
        let checkedMachineName = (value.clubName + '-1').split(',')
        let checkedMachines = checkedMachineIds.map((v, i) => {
          return {
            id: v,
            name: checkedMachineName[i]
          }
        })
        if (deletedMachines[value.id]) {
          deletedMachines[value.id].forEach((v) => {
            let index = checkedMachines.map(v => v.id).indexOf(v.machine_id)
            if (index !== -1) {
              checkedMachines.splice(index, 1)
            }
          })
        }
        let firstStore
        let storeLength = 0
        storesIds.forEach((v, i) => {
          // 没有选中的话，剔除门店
          if (checkedMachines.map(v => v.name ? v.name.split('-')[0] : '').indexOf(storesName[i]) === -1) {
            return
          }
          storeLength++
          firstStore = firstStore || v
          let store = {
            storeName: storesName[i],
            storeId: v,
            folderId: value.id,
            folderName: value.folderName,
            unlimited_time: value.unlimitedTime,
            status: value.status,
            startTime: value.startTime,
            endTime: value.endTime,
            rePlayDate: value.rePlayDate,
            rePlayTime: value.rePlayTime,
            table_index: value.index,
            machines: [],
            checked_machine_ids: checkedMachines.map(v => v.id).join(','),
            checked_machine_name: checkedMachineName,
            index: storeLength - 1
          }
          storesObject[v] = store
          storesArray.push(store)
        })
        if (firstStore) {
          storesObject[firstStore]['spanSign'] = storeLength > 3 ? storeLength + 1 : storeLength
          if (storeLength > 3) {
            storesArray.push({
              machines: [],
              more: true,
              folderId: value.id,
              index: -1
            })
          }
          machinesName.forEach((v, i) => {
            let storeName = v.split('-')[0]
            let storeId = storesIds[storesName.indexOf(storeName)]
            if (!storesObject[storeId]) {
              return
            }
            storesObject[storeId]['machines'].push({
              machineName: v,
              machineId: machineIds[i],
              storeName,
              storeId
            })
          })
        } else {
          // 全都被剔除了
          storesArray.push({
            machines: [],
            spanSign: 1,
            empty: true,
            folderId: value.id,
            folderName: value.folderName,
            unlimited_time: value.unlimitedTime,
            status: value.status,
            startTime: value.startTime,
            endTime: value.endTime,
            rePlayDate: value.rePlayDate,
            rePlayTime: value.rePlayTime,
            table_index: value.index,
            index: 1
          })
        }
      })
      return storesArray
    },
    type: function () {
      switch (this.$route.params.type) {
        case 'top':
          return 0
        case 'middle':
          return 1
        case 'bottom':
          return 2
      }
    },
    content_type: function () {
      switch (this.$route.query.classify) {
        case 'picture':
          return 0
        case 'video':
          return 1
      }
    }
  },
  watch: {
    '$route': function () {
      this.getList(Object.assign({}, this.$route.query))
    }
  },
  beforeCreate () {
    let {classify, pageNum, pageSize} = this.$route.query
    if ((classify !== 'picture') && (classify !== 'video') && Object.is(pageNum, undefined) && Object.is(pageSize, undefined)) {
      this.$router.replace({
        path: this.$route.path,
        query: {
          classify: 'picture',
          pageNum: 1,
          pageSize: 10
        }
      })
    }
  },
  mounted () {
    this.getList(Object.assign({}, this.$route.query))
    window.onresize = throttle(() => {
      this.checkSpread()
    }, 1000)
    let firstCheck = setInterval(() => {
      if (this.checkSpread() !== false) {
        clearInterval(firstCheck)
      }
    }, 500)
  },
  destroyed () {
    window.onresize = null
  },
  methods: {
    handleMore (id, index) {
      this.showOption = this.showOption ? false : id
      if (this.$refs.folder && this.$refs.folder[index] && (typeof this.$refs.folder[index].focus === 'function')) {
        this.$refs.folder[index].focus()
      }
    },
    handleName (name) {
      if (name.length > 13) {
        return name.substr(0, 13) + '...'
      } else {
        return name
      }
    },
    handleDelete (folder) {
      this.$confirm({
        title: '广告位展示',
        brief: '确定要删除此文件夹吗？',
        confirm: (dialog) => {
          console.log(folder)
          dialog.loading = true
          deleteFolder({
            empty: folder.empty ? 1 : '',
            id: folder.id,
            type: this.type
          }).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.getList(Object.assign({}, this.$route.query))
              this.$message({
                message: '删除成功',
                type: 'success'
              })
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    changeStatus (folder) {
      if (Number(folder.status) === 1) {
        this.$message({
          message: '停播处理中'
        })
        let data = {
          id: folder.id,
          type: this.type,
          status: String(folder.status) === '0' ? '1' : '0'
        }
        stopPlayFolder(data).then(res => {
          if (Number(res.code) === 200) {
            this.$message({
              message: `修改成功，已${String(folder.status) === '0' ? '播放' : '停播'}文件夹`,
              type: 'success'
            })
            this.getList(Object.assign({}, this.$route.query))
          } else {
            this.$message({
              message: res.message,
              type: 'warning'
            })
          }
        })
      } else {
        this.showDetail = true
        this.detailFolder = folder.id
        this.isReplay = true
      }
    },
    headerClick (column) {
      if (column.property === 'machines') {
        if (this.canSpread) {
          this.isSpread = !this.isSpread
        }
      }
    },
    cellClick (row) {
      if (row.more) {
        let folderId = row.folderId
        let index = this.spreadFolder.indexOf(folderId)
        if (index === -1) {
          this.spreadFolder.push(folderId)
        } else {
          this.spreadFolder.splice(index, 1)
        }
      }
    },
    checkSpread () {
      if (this.isSpread) {
        return
      }
      let keys = Object.keys(this.$refs).filter(v => v.split('-')[0] === 'ul' && this.$refs[v])
      let uls = keys.map(v => this.$refs[v])
      if (uls.length === 0) {
        return false
      }
      let needSpread = uls.map(v => v.offsetHeight).some(v => v !== 40)
      if (needSpread) {
        this.canSpread = uls.map(v => {
          return Array.prototype.slice.call(v.childNodes).reduce((x, y) => x + y.offsetWidth + 40, 0)
        }).reduce((x, y) => x > y ? x : y)
        let ids = keys.map(v => v.split('-')[1])
        let idNeedSpread = []
        uls.forEach((v, i) => {
          if (v.offsetHeight !== 40) {
            idNeedSpread.push(ids[i])
          }
        })
        this.idNeedSpread = idNeedSpread
      } else {
        this.canSpread = false
      }
    },
    arraySpanMethod ({row, column, rowIndex, columnIndex}) {
      if ((columnIndex !== 2) && (columnIndex !== 3)) {
        if (row.spanSign) {
          let rowspan = row.spanSign
          return {
            rowspan: rowspan,
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      } else {
        if (row.more) {
          return {
            rowspan: columnIndex === 2 ? 1 : 0,
            colspan: columnIndex === 2 ? 2 : 0
          }
        } else {
          if ((this.spreadFolder.indexOf(row.folderId) === -1) && (row.index > 2)) {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        }
      }
    },
    easyQuery (data = {}) {
      let prevQuery = this.$route.query
      let query = Object.assign({}, prevQuery, data)
      this.$router.push({path: this.$route.path, query})
    },
    handleClassify (classify) {
      this.easyQuery({
        pageNum: 1,
        classify
      })
    },
    getList (data = {}) {
      this.isLoading = true
      data['type'] = this.type
      data['content_type'] = this.content_type
      delete data['classify']
      data['pageNum'] = data['pageNum'] || 1
      data['pageSize'] = data['pageSize'] || 10
      this.listData.data = null
      getFolderList(data).then((res) => {
        if (Number(res.code) === 200) {
          if (res.data.length === 0) {
            this.isEmpty = true
          } else {
            this.isEmpty = false
            this.listData = this.countIndex(res)
          }
        } else if (Number(res.code) === 201) {
          this.isEmpty = true
        } else if (Number(res.code) === 202) {
          this.isNoPower = true
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
        this.isLoading = false
      })
    },
    countIndex (res) {
      let {pageSize, pageNum, data} = res
      data.forEach((v, i) => {
        v.index = (pageSize * (Number(pageNum) - 1)) + i + 1
      })
      return res
    },
    pageNumChange (pageNum) {
      this.easyQuery({
        pageNum: String(pageNum)
      })
    },
    pageSizeChange (pageSize) {
      this.easyQuery({
        pageNum: String(1),
        pageSize: pageSize
      })
    },
    handleAdd () {
      this.showDetail = true
      this.detailFolder = false
    },
    handleEdit (folder) {
      this.showDetail = true
      this.detailFolder = folder.id
      this.isReplay = false
    },
    handleConfirm (isPass) {
      this.showDetail = false
      if (isPass) {
        this.getList(Object.assign({}, this.$route.query))
      }
    }
  }
}
</script>
