      left 0
      width 100%
      height 100%
      display flex
      align-items center
      justify-content center
      cursor pointer
  .modal-class-icon
    display inline-block
    background-size 100% 100%
    background url('~@/assets/modal_icon_2.png') no-repeat center
    width 40px
    height 40px
</style>



