<template>
  <transition name="fade">
    <div class="modal-container" v-show="visible">
      <div class="modal-box" :style="'width:'+Number(width)+'px'" @click.stop>
        <div class="modal-header">
          <div class="modal-icon">
            <slot v-if="$slots.icon" name="icon"/>
            <i v-else></i>
          </div>
          <div class="modal-title">{{title}}</div>
        </div>
        <div class="modal-body">
          <slot/>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  props: {
    visible: Boolean,
    title: String,
    width: {
      type: [String, Number],
      default: 520
    }
  },
  mounted () {
    document.body.appendChild(this.$el)
  }
}
</script>
<style lang='stylus' scoped>
  .modal-container
    position fixed
    z-index 500
    top 0
    left 0
    width 100%
    height 100%
    background rgba(0, 0, 0, .2)
    display flex
    align-items center
    justify-content center
  .modal-box
    width 520px
    min-height 160px
    background white
    border-radius 8px
    box-shadow 0 2px 8px 0 rgba(0, 0, 0, 0.2)
  .modal-header
    height 80px
    display flex
    flex-direction row
    align-items center
  .modal-icon
    width 110px
    padding-left 30px
    display flex
    flex-direction row
    align-items center
    & > i
      display inline-block
      background-size 100% 100%
      background url('~@/assets/modal_icon_1.png') no-repeat center
      width 50px
      height 40px
  .modal-title
    flex 1
    color #333333
    font-size 16px
  .modal-close-button
    padding 10px
    padding-right 30px
    display flex
    justify-content center
    align-items center
    cursor pointer
  .modal-close-icon
    display inline-block
    background-size 100% 100%
    width 12px
    height 12px
    background url('~@/assets/blue_close.png') no-repeat center
  .modal-body
    position relative
</style>



